import React from 'react'
import Link from 'next/link'
//export default({children})=>

class Layout extends React.Component{
render(){
    var token=true
    return(
        <div>
                <nav className="nav">
                    <div className="navdiv">
                        <div className="brand">
                            <span className="header_font">            
                                <Link href="/"><span style={{color:'black'}}><b>Fasigner</b></span> </Link>
                            </span>
                        </div>
                    { token ? (
                        <div className="profile_info">         
                        <span className="top_p"><Link href='/user/ajay'><img className="logo" style={style} src={profile} alt="sdf"  /> </Link></span>
                        <span className="top_p"><Link herf="/message"><img src={msg} alt="sdf" className="logo" style={style} /> </Link></span>
                        <span className="top_p"><Link href="/notify/"><img src={notify} alt="sdf" className="logo" style={style} /> </Link></span>
                        <span className="top_p"><Link href="#" onClick={this.logout} ><img src={logout} alt="sdf" className="logo" style={style} /> </Link></span>
                        </div>
                        ): (
                            <div className="profile_info">                    
                            <span className="top_p"><Link to="/login">Login</Link></span>
                            </div>
                        )

                    }
        {/*                    <span className="top_p"><Link to="/logout">Logout</Link></span>
                        <span className="top_p"><Link to="/login">Login</Link></span>*/}
                    </div>
                </nav>


            <main>
                { children }
            </main>
        </div>
    )
    }
}
export default ({children})=>Layout


